export const gradeMapping: Record<string, number> = {
  "A+": 10,
  "A": 9,
  "B+": 8,
  "B": 7,
  "C+": 6,
  "C": 5,
  "D": 4,
  "F": 0,
};

export interface SubjectData {
  subject: string;
  credit: number;
  grade: string;
}

export interface StudentData {
  studentName?: string;
  registerNumber?: string;
  subjects: SubjectData[];
}

export interface CalculationResult {
  studentName?: string;
  registerNumber?: string;
  cgpa: number;
  failed: boolean;
  failedSubjects?: string[];
  subjects?: Array<{ subject: string; credit: number; grade: string; gradePoint: number }>;
}

export function calculateStudentCGPA(student: StudentData): CalculationResult {
  let totalCredits = 0;
  let totalPoints = 0;
  let failed = false;
  const failedSubjects: string[] = [];
  const subjectDetails: Array<{ subject: string; credit: number; grade: string; gradePoint: number }> = [];

  student.subjects.forEach((sub) => {
    const credit = Number(sub.credit);
    const gradePoint = gradeMapping[sub.grade.toUpperCase()];
    
    if (isNaN(credit) || gradePoint === undefined) {
      throw new Error(`Invalid credit or grade for subject ${sub.subject}`);
    }
    
    totalCredits += credit;
    totalPoints += credit * gradePoint;
    
    // Define failure criteria: grade point below 5 (or explicitly F)
    if (gradePoint < 5) {
      failed = true;
      failedSubjects.push(sub.subject);
    }

    subjectDetails.push({
      subject: sub.subject,
      credit: credit,
      grade: sub.grade.toUpperCase(),
      gradePoint: gradePoint
    });
  });

  const cgpa = totalCredits > 0 ? totalPoints / totalCredits : 0;
  
  return {
    studentName: student.studentName,
    registerNumber: student.registerNumber,
    cgpa,
    failed,
    failedSubjects,
    subjects: subjectDetails
  };
}

export function getGradeFromPoints(points: number): string {
  if (points >= 9.5) return "A+";
  if (points >= 8.5) return "A";
  if (points >= 7.5) return "B+";
  if (points >= 6.5) return "B";
  if (points >= 5.5) return "C+";
  if (points >= 4.5) return "C";
  if (points >= 3.5) return "D";
  return "F";
}

export function getCGPAGrade(cgpa: number): string {
  return getGradeFromPoints(cgpa);
}
