"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { Label } from "@/components/ui/label";
import { calculateStudentCGPA, gradeMapping, type StudentData } from "@/lib/cgpaCalculator";
import { generateCSVReport } from "@/lib/reportGenerator";
import { parseFile } from "@/lib/fileParser";
import { parseDepartmentCSV } from "@/lib/departmentParser";
import dynamic from "next/dynamic";

// Import chart components (client-side only)
const CGPAPieChart = dynamic(() => import("@/components/cgpa/CGPAPieChart"), { ssr: false });
const CGPABarChart = dynamic(() => import("@/components/cgpa/CGPABarChart"), { ssr: false });
const SubjectFailureChart = dynamic(() => import("@/components/cgpa/SubjectFailureChart"), { ssr: false });
const OverallPerformanceChart = dynamic(() => import("@/components/cgpa/OverallPerformanceChart"), { ssr: false });

interface ManualEntry {
  subjectCode: string;
  subjectName: string;
  credit: string;
  grade: string;
}

interface CalculationResult {
  studentName?: string;
  registerNumber?: string;
  cgpa: number;
  failed: boolean;
  failedSubjects?: string[];
  subjects?: Array<{ subject: string; credit: number; grade: string; gradePoint: number }>;
}

export default function CGPACalculatorPage() {
  const [activeTab, setActiveTab] = useState("manual");
  const [manualEntries, setManualEntries] = useState<ManualEntry[]>([
    { subjectCode: "", subjectName: "", credit: "", grade: "" }
  ]);
  const [studentName, setStudentName] = useState("");
  const [registerNumber, setRegisterNumber] = useState("");
  const [uploadFile, setUploadFile] = useState<File | null>(null);
  const [useDepartmentFormat, setUseDepartmentFormat] = useState(false);
  const [results, setResults] = useState<CalculationResult[]>([]);
  const [error, setError] = useState("");
  const [loading, setLoading] = useState(false);

  const gradeOptions = Object.keys(gradeMapping);

  const handleManualEntryChange = (index: number, field: keyof ManualEntry, value: string) => {
    const entries = [...manualEntries];
    entries[index] = { ...entries[index], [field]: value };
    setManualEntries(entries);
  };

  const addManualEntry = () => {
    setManualEntries([...manualEntries, { subjectCode: "", subjectName: "", credit: "", grade: "" }]);
  };

  const removeManualEntry = (index: number) => {
    if (manualEntries.length > 1) {
      const entries = manualEntries.filter((_, i) => i !== index);
      setManualEntries(entries);
    }
  };

  const handleFileUpload = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (file) {
      const fileExtension = file.name.split('.').pop()?.toLowerCase();
      if (['csv', 'xlsx', 'xls'].includes(fileExtension || '')) {
        setUploadFile(file);
        setError("");
      } else {
        setError("Please upload CSV or Excel files only.");
        setUploadFile(null);
      }
    }
  };

  const handleCalculation = async () => {
    try {
      setError("");
      setLoading(true);
      let calculatedResults: CalculationResult[] = [];

      if (activeTab === "manual") {
        // Validate manual entries
        const validEntries = manualEntries.filter(entry => 
          entry.subjectCode.trim() && entry.subjectName.trim() && entry.credit.trim() && entry.grade.trim()
        );

        if (validEntries.length === 0) {
          throw new Error("Please add at least one complete subject entry with subject code and name.");
        }

        // Validate credits are numbers
        for (let i = 0; i < validEntries.length; i++) {
          const credit = Number(validEntries[i].credit);
          if (isNaN(credit) || credit <= 0) {
            throw new Error(`Invalid credit value in row ${i + 1}. Please enter a positive number.`);
          }
        }

        const studentData: StudentData = {
          studentName: studentName.trim() || "Student",
          registerNumber: registerNumber.trim(),
          subjects: validEntries.map(entry => ({
            subject: `${entry.subjectCode.trim()} - ${entry.subjectName.trim()}`,
            credit: Number(entry.credit),
            grade: entry.grade.toUpperCase()
          }))
        };

        const result = calculateStudentCGPA(studentData);
        calculatedResults = [result];
      } else {
        // Handle file upload
        if (!uploadFile) {
          throw new Error("Please select a CSV or Excel file to upload.");
        }

        const studentsData = useDepartmentFormat 
          ? await parseDepartmentCSV(uploadFile)
          : await parseFile(uploadFile);
        calculatedResults = studentsData.map((student: StudentData) => calculateStudentCGPA(student));
      }

      setResults(calculatedResults);
    } catch (err: any) {
      setError(err.message || "An error occurred during calculation");
    } finally {
      setLoading(false);
    }
  };

  const handleDownload = () => {
    if (results.length === 0) return;
    
    const csvContent = generateCSVReport(results);
    const blob = new Blob([csvContent], { type: "text/csv;charset=utf-8;" });
    const url = URL.createObjectURL(blob);
    const link = document.createElement("a");
    link.setAttribute("href", url);
    link.setAttribute("download", "cgpa_report.csv");
    link.style.display = "none";
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    URL.revokeObjectURL(url);
  };

  const clearResults = () => {
    setResults([]);
    setError("");
  };

  return (
    <div className="min-h-screen bg-gray-50 py-8">
      <div className="max-w-6xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="text-center mb-8">
          <h1 className="text-4xl font-bold text-gray-900 mb-2">CGPA Calculator</h1>
          <p className="text-lg text-gray-600">Professional-grade CGPA calculator for college students</p>
        </div>

        <Card className="mb-8">
          <CardHeader>
            <CardTitle>Calculate CGPA</CardTitle>
            <CardDescription>
              Enter subject details manually or upload a CSV file with student data
            </CardDescription>
          </CardHeader>
          <CardContent>
            <Tabs value={activeTab} onValueChange={setActiveTab}>
              <TabsList className="grid w-full grid-cols-2">
                <TabsTrigger value="manual">Manual Entry</TabsTrigger>
                <TabsTrigger value="upload">Upload CSV</TabsTrigger>
              </TabsList>

              <TabsContent value="manual" className="space-y-6">
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div>
                    <Label htmlFor="studentName">Student Name</Label>
                    <Input
                      id="studentName"
                      placeholder="Enter student name"
                      value={studentName}
                      onChange={(e) => setStudentName(e.target.value)}
                    />
                  </div>
                  <div>
                    <Label htmlFor="registerNumber">Register Number (Optional)</Label>
                    <Input
                      id="registerNumber"
                      placeholder="Enter register number"
                      value={registerNumber}
                      onChange={(e) => setRegisterNumber(e.target.value)}
                    />
                  </div>
                </div>

                <div className="space-y-4">
                  <Label className="text-base font-semibold">Subject Details</Label>
                  {manualEntries.map((entry, index) => (
                    <div key={index} className="grid grid-cols-1 md:grid-cols-5 gap-4 p-4 border rounded-lg">
                      <div>
                        <Label htmlFor={`subject-code-${index}`}>Subject Code</Label>
                        <Input
                          id={`subject-code-${index}`}
                          placeholder="e.g., 21CH103"
                          value={entry.subjectCode}
                          onChange={(e) => handleManualEntryChange(index, "subjectCode", e.target.value)}
                        />
                      </div>
                      <div>
                        <Label htmlFor={`subject-name-${index}`}>Subject Name</Label>
                        <Input
                          id={`subject-name-${index}`}
                          placeholder="e.g., Chemistry"
                          value={entry.subjectName}
                          onChange={(e) => handleManualEntryChange(index, "subjectName", e.target.value)}
                        />
                      </div>
                      <div>
                        <Label htmlFor={`credit-${index}`}>Credits</Label>
                        <Input
                          id={`credit-${index}`}
                          placeholder="e.g., 4"
                          type="number"
                          min="1"
                          value={entry.credit}
                          onChange={(e) => handleManualEntryChange(index, "credit", e.target.value)}
                        />
                      </div>
                      <div>
                        <Label htmlFor={`grade-${index}`}>Grade</Label>
                        <Select
                          value={entry.grade}
                          onValueChange={(value) => handleManualEntryChange(index, "grade", value)}
                        >
                          <SelectTrigger>
                            <SelectValue placeholder="Select grade" />
                          </SelectTrigger>
                          <SelectContent>
                            {gradeOptions.map((grade) => (
                              <SelectItem key={grade} value={grade}>
                                {grade} ({gradeMapping[grade]} points)
                              </SelectItem>
                            ))}
                          </SelectContent>
                        </Select>
                      </div>
                      <div className="flex items-end">
                        <Button
                          variant="outline"
                          size="sm"
                          onClick={() => removeManualEntry(index)}
                          disabled={manualEntries.length === 1}
                          className="w-full"
                        >
                          Remove
                        </Button>
                      </div>
                    </div>
                  ))}
                  <Button onClick={addManualEntry} variant="outline" className="w-full">
                    Add Subject
                  </Button>
                </div>
              </TabsContent>

              <TabsContent value="upload" className="space-y-6">
                <div>
                  <Label htmlFor="csvFile">Upload CSV/Excel File</Label>
                  <div className="flex items-center space-x-4 mb-4">
                    <div className="flex items-center space-x-2">
                      <input
                        type="radio"
                        id="standard-format"
                        name="format"
                        checked={!useDepartmentFormat}
                        onChange={() => setUseDepartmentFormat(false)}
                        className="h-4 w-4"
                      />
                      <Label htmlFor="standard-format" className="text-sm">Standard Format</Label>
                    </div>
                    <div className="flex items-center space-x-2">
                      <input
                        type="radio"
                        id="department-format"
                        name="format"
                        checked={useDepartmentFormat}
                        onChange={() => setUseDepartmentFormat(true)}
                        className="h-4 w-4"
                      />
                      <Label htmlFor="department-format" className="text-sm">Department Format</Label>
                    </div>
                  </div>
                  <Input
                    id="csvFile"
                    type="file"
                    accept=".csv,.xlsx,.xls"
                    onChange={handleFileUpload}
                    className="mt-2"
                  />
                  <p className="text-sm text-gray-500 mt-2">
                    {useDepartmentFormat 
                      ? "Department format: S.No, Roll.No, Register No, Name, Subject Codes as columns (21CH103, 21CS103, etc.)"
                      : "Standard format: Student Name, Register Number, Subject, Credits, Grade"
                    }
                  </p>
                  {useDepartmentFormat && (
                    <div className="mt-2 p-3 bg-blue-50 rounded-md">
                      <p className="text-sm text-blue-800">
                        <strong>Department Format Example:</strong><br />
                        S.No,Roll.No,Register No,Name of the Student,21CH103,21CS103,21EE104,21EN102,21MA103,21ME101,21PH103,21TA102,21CS104,21EM101,Credits
                      </p>
                    </div>
                  )}
                </div>
                {uploadFile && (
                  <Alert>
                    <AlertDescription>
                      File selected: {uploadFile.name} ({(uploadFile.size / 1024).toFixed(2)} KB)
                    </AlertDescription>
                  </Alert>
                )}
              </TabsContent>
            </Tabs>

            {error && (
              <Alert className="mt-4" variant="destructive">
                <AlertDescription>{error}</AlertDescription>
              </Alert>
            )}

            <div className="flex gap-4 mt-6">
              <Button 
                onClick={handleCalculation} 
                disabled={loading}
                className="flex-1"
              >
                {loading ? "Calculating..." : "Calculate CGPA"}
              </Button>
              {results.length > 0 && (
                <Button onClick={clearResults} variant="outline">
                  Clear Results
                </Button>
              )}
            </div>
          </CardContent>
        </Card>

        {results.length > 0 && (
          <>
            <Card className="mb-8">
              <CardHeader className="flex flex-row items-center justify-between">
                <div>
                  <CardTitle>CGPA Results</CardTitle>
                  <CardDescription>
                    Calculated CGPA and failure analysis for {results.length} student(s)
                  </CardDescription>
                </div>
                <Button onClick={handleDownload}>
                  Download Report
                </Button>
              </CardHeader>
              <CardContent>
                <Table>
                  <TableHeader>
                    <TableRow>
                      <TableHead>Student Name</TableHead>
                      <TableHead>Register Number</TableHead>
                      <TableHead>CGPA</TableHead>
                      <TableHead>Status</TableHead>
                      <TableHead>Failed Subjects</TableHead>
                    </TableRow>
                  </TableHeader>
                  <TableBody>
                    {results.map((result, index) => (
                      <TableRow key={index}>
                        <TableCell className="font-medium">
                          {result.studentName || "N/A"}
                        </TableCell>
                        <TableCell>{result.registerNumber || "N/A"}</TableCell>
                        <TableCell className="font-semibold">
                          {result.cgpa.toFixed(2)}
                        </TableCell>
                        <TableCell>
                          <span className={`px-2 py-1 rounded-full text-xs font-medium ${
                            result.failed 
                              ? "bg-red-100 text-red-800" 
                              : "bg-green-100 text-green-800"
                          }`}>
                            {result.failed ? "Failed" : "Passed"}
                          </span>
                        </TableCell>
                        <TableCell>
                          {result.failedSubjects && result.failedSubjects.length > 0
                            ? result.failedSubjects.join(", ")
                            : "None"
                          }
                        </TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </CardContent>
            </Card>

            <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
              <CGPAPieChart data={results} />
              <CGPABarChart data={results} />
              <SubjectFailureChart data={results} />
              <OverallPerformanceChart data={results} />
            </div>
          </>
        )}
      </div>
    </div>
  );
}
