export interface ReportData {
  studentName?: string;
  registerNumber?: string;
  cgpa: number;
  failed: boolean;
  failedSubjects?: string[];
  subjects?: Array<{ subject: string; credit: number; grade: string; gradePoint: number }>;
}

export function generateCSVReport(data: ReportData[]): string {
  const header = "Student Name,Register Number,CGPA,Status,Failed Subjects,Total Credits,Grade\n";
  
  const rows = data.map(item => {
    const totalCredits = item.subjects?.reduce((sum, sub) => sum + sub.credit, 0) || 0;
    const grade = getCGPAGrade(item.cgpa);
    const failedSubjectsStr = item.failedSubjects && item.failedSubjects.length > 0 
      ? item.failedSubjects.join("; ") 
      : "None";
    
    return `"${item.studentName || "N/A"}","${item.registerNumber || "N/A"}",${item.cgpa.toFixed(2)},${item.failed ? "Failed" : "Passed"},"${failedSubjectsStr}",${totalCredits},"${grade}"`;
  });
  
  return header + rows.join("\n");
}

export function generateDetailedCSVReport(data: ReportData[]): string {
  let csvContent = "Student Name,Register Number,Subject,Credits,Grade,Grade Points,CGPA,Overall Status\n";
  
  data.forEach(student => {
    if (student.subjects && student.subjects.length > 0) {
      student.subjects.forEach(subject => {
        csvContent += `"${student.studentName || "N/A"}","${student.registerNumber || "N/A"}","${subject.subject}",${subject.credit},"${subject.grade}",${subject.gradePoint},${student.cgpa.toFixed(2)},"${student.failed ? "Failed" : "Passed"}"\n`;
      });
    } else {
      csvContent += `"${student.studentName || "N/A"}","${student.registerNumber || "N/A"}","N/A","N/A","N/A","N/A",${student.cgpa.toFixed(2)},"${student.failed ? "Failed" : "Passed"}"\n`;
    }
  });
  
  return csvContent;
}

export function generateFailureAnalysisReport(data: ReportData[]): string {
  const failedStudents = data.filter(student => student.failed);
  
  if (failedStudents.length === 0) {
    return "Student Name,Register Number,Failed Subjects,CGPA\nNo failed students found";
  }
  
  const header = "Student Name,Register Number,Failed Subjects,CGPA\n";
  const rows = failedStudents.map(student => {
    const failedSubjectsStr = student.failedSubjects && student.failedSubjects.length > 0 
      ? student.failedSubjects.join("; ") 
      : "Unknown";
    
    return `"${student.studentName || "N/A"}","${student.registerNumber || "N/A"}","${failedSubjectsStr}",${student.cgpa.toFixed(2)}`;
  });
  
  return header + rows.join("\n");
}

function getCGPAGrade(cgpa: number): string {
  if (cgpa >= 9.5) return "A+";
  if (cgpa >= 8.5) return "A";
  if (cgpa >= 7.5) return "B+";
  if (cgpa >= 6.5) return "B";
  if (cgpa >= 5.5) return "C+";
  if (cgpa >= 4.5) return "C";
  if (cgpa >= 3.5) return "D";
  return "F";
}

export function downloadCSV(content: string, filename: string): void {
  const blob = new Blob([content], { type: "text/csv;charset=utf-8;" });
  const url = URL.createObjectURL(blob);
  const link = document.createElement("a");
  link.setAttribute("href", url);
  link.setAttribute("download", filename);
  link.style.display = "none";
  document.body.appendChild(link);
  link.click();
  document.body.removeChild(link);
  URL.revokeObjectURL(url);
}
