import { StudentData } from './cgpaCalculator';

export async function parseDepartmentCSV(file: File): Promise<StudentData[]> {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    
    reader.onload = (event) => {
      try {
        const csvText = event.target?.result as string;
        const students = parseDepartmentFormat(csvText);
        resolve(students);
      } catch (error) {
        reject(new Error(`Failed to parse department CSV: ${error instanceof Error ? error.message : 'Unknown error'}`));
      }
    };
    
    reader.onerror = () => {
      reject(new Error('Failed to read the file'));
    };
    
    reader.readAsText(file);
  });
}

function parseDepartmentFormat(csvText: string): StudentData[] {
  const lines = csvText.trim().split('\n');
  
  if (lines.length < 2) {
    throw new Error('CSV file must contain at least a header row and one data row');
  }
  
  const headers = lines[0].split(',').map(h => h.trim());
  
  // Find key columns with more flexible matching
  const nameIndex = headers.findIndex(h => 
    h.toLowerCase().includes('name') && !h.toLowerCase().includes('subject')
  );
  const rollNoIndex = headers.findIndex(h => 
    h.toLowerCase().includes('roll') || h.toLowerCase().includes('roll.no') || h.toLowerCase().includes('roll no')
  );
  const registerNoIndex = headers.findIndex(h => 
    h.toLowerCase().includes('register') || h.toLowerCase().includes('register no') || h.toLowerCase().includes('register.no')
  );
  const creditsIndex = headers.findIndex(h => 
    h.toLowerCase().includes('credit')
  );
  
  // Use roll number if register number is not found
  const studentIdIndex = registerNoIndex !== -1 ? registerNoIndex : rollNoIndex;
  
  if (nameIndex === -1 || studentIdIndex === -1) {
    throw new Error('CSV must contain Student Name and either Register Number or Roll Number columns');
  }
  
  // Find subject columns (columns that look like subject codes)
  const subjectColumns = headers.map((header, index) => ({
    code: header,
    index: index
  })).filter(col => {
    const header = col.code.toUpperCase();
    // Match patterns like 21CH103, 21CS103, 21EE104, etc.
    return header.match(/^\d{2}[A-Z]{2,3}\d{3,4}$/) || 
           header.match(/^[A-Z]{2,3}\d{3,4}$/) ||
           header.match(/^\d{2}[A-Z]{2}\d{3}$/);
  });
  
  if (subjectColumns.length === 0) {
    throw new Error('No subject code columns found. Expected columns like 21CH103, 21CS103, etc.');
  }
  
  const students: StudentData[] = [];
  
  for (let i = 1; i < lines.length; i++) {
    const row = lines[i].split(',').map(cell => cell.trim());
    
    if (row.length < Math.max(nameIndex, registerNoIndex) + 1) {
      console.warn(`Skipping row ${i + 1}: insufficient columns`);
      continue;
    }
    
    const studentName = row[nameIndex];
    const registerNumber = row[studentIdIndex];
    
    if (!studentName || !registerNumber) {
      console.warn(`Skipping row ${i + 1}: missing student name or register number`);
      continue;
    }
    
    const subjects: Array<{subject: string, credit: number, grade: string}> = [];
    
    // Default credits per subject (can be overridden if credits column exists)
    let defaultCredits = 4;
    if (creditsIndex !== -1 && row[creditsIndex]) {
      const credits = parseInt(row[creditsIndex]);
      if (!isNaN(credits) && credits > 0) {
        defaultCredits = credits;
      }
    }
    
    // Process each subject
    subjectColumns.forEach(subjectCol => {
      const grade = row[subjectCol.index];
      if (grade && grade !== '' && grade !== '-') {
        subjects.push({
          subject: subjectCol.code,
          credit: defaultCredits,
          grade: grade.toUpperCase()
        });
      }
    });
    
    if (subjects.length > 0) {
      students.push({
        studentName,
        registerNumber,
        subjects
      });
    }
  }
  
  if (students.length === 0) {
    throw new Error('No valid student data found in the CSV file');
  }
  
  return students;
}

export function createDepartmentTemplate(): string {
  return `S.No,Roll.No,Register No,Name of the Student,21CH103,21CS103,21EE104,21EN102,21MA103,21ME101,21PH103,21TA102,21CS104,21EM101,Credits
1,24ITB01,913124205001,Student Name 1,A,A,A,B+,B+,A,A,A+,A+,O,4
2,24ITB02,913124205002,Student Name 2,B+,A,A,B,B,A,A,A,A+,O,4
3,24ITB03,913124205003,Student Name 3,A+,A+,A,A,A+,A+,A+,A+,O,4`;
}
