import Papa from 'papaparse';
import * as XLSX from 'xlsx';
import { StudentData } from './cgpaCalculator';

export async function parseFile(file: File): Promise<StudentData[]> {
  const fileExtension = file.name.split('.').pop()?.toLowerCase();
  
  if (fileExtension === 'csv') {
    return parseCSVFile(file);
  } else if (['xlsx', 'xls'].includes(fileExtension || '')) {
    return parseExcelFile(file);
  } else {
    throw new Error('Unsupported file format. Please upload CSV or Excel files only.');
  }
}

export async function parseCSVFile(file: File): Promise<StudentData[]> {
  return new Promise((resolve, reject) => {
    Papa.parse(file, {
      header: true,
      skipEmptyLines: true,
      transformHeader: (header: string) => header.trim().toLowerCase(),
      complete: (results) => {
        try {
          const students = processData(results.data as any[]);
          resolve(students);
        } catch (error) {
          reject(new Error(`Failed to parse CSV file: ${error instanceof Error ? error.message : 'Unknown error'}`));
        }
      },
      error: (error) => {
        reject(new Error(`CSV parsing error: ${error.message}`));
      }
    });
  });
}

async function parseExcelFile(file: File): Promise<StudentData[]> {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    
    reader.onload = (e) => {
      try {
        const data = new Uint8Array(e.target?.result as ArrayBuffer);
        const workbook = XLSX.read(data, { type: 'array' });
        
        // Get the first worksheet
        const sheetName = workbook.SheetNames[0];
        const worksheet = workbook.Sheets[sheetName];
        
        // Convert to JSON
        const jsonData = XLSX.utils.sheet_to_json(worksheet, { header: 1 }) as any[][];
        
        if (jsonData.length < 2) {
          throw new Error('Excel file must contain at least a header row and one data row');
        }
        
        const headers = jsonData[0].map((header: string) => header.toString().trim().toLowerCase());
        const dataRows = jsonData.slice(1);
        
        const students = processExcelData(headers, dataRows);
        resolve(students);
      } catch (error) {
        reject(new Error(`Failed to parse Excel file: ${error instanceof Error ? error.message : 'Unknown error'}`));
      }
    };
    
    reader.onerror = () => {
      reject(new Error('Failed to read the Excel file'));
    };
    
    reader.readAsArrayBuffer(file);
  });
}

function processData(data: any[]): StudentData[] {
  if (data.length === 0) {
    throw new Error('CSV file is empty or contains no valid data');
  }

  // Find column mappings
  const firstRow = data[0];
  const headers = Object.keys(firstRow);
  
  const studentNameCol = findColumn(headers, ['student name', 'name', 'student']);
  const registerNumberCol = findColumn(headers, ['register number', 'registration number', 'reg no', 'register no']);
  const subjectCol = findColumn(headers, ['subject', 'subject name', 'course']);
  const creditCol = findColumn(headers, ['credit', 'credits', 'credit hours']);
  const gradeCol = findColumn(headers, ['grade', 'grades']);

  if (!studentNameCol || !subjectCol || !creditCol || !gradeCol) {
    throw new Error('CSV file must contain columns: Student Name, Subject, Credits, Grade');
  }

  // Group data by student
  const studentMap = new Map<string, StudentData>();

  data.forEach((row, index) => {
    const studentName = row[studentNameCol]?.toString().trim();
    const registerNumber = registerNumberCol ? row[registerNumberCol]?.toString().trim() : '';
    const subject = row[subjectCol]?.toString().trim();
    const creditStr = row[creditCol]?.toString().trim();
    const grade = row[gradeCol]?.toString().trim();

    if (!studentName || !subject || !creditStr || !grade) {
      console.warn(`Skipping row ${index + 2}: missing required data`);
      return;
    }

    const credit = parseFloat(creditStr);
    if (isNaN(credit) || credit <= 0) {
      throw new Error(`Invalid credit value "${creditStr}" in row ${index + 2}`);
    }

    const studentKey = `${studentName}_${registerNumber}`;

    if (!studentMap.has(studentKey)) {
      studentMap.set(studentKey, {
        studentName,
        registerNumber: registerNumber || undefined,
        subjects: []
      });
    }

    const student = studentMap.get(studentKey)!;
    student.subjects.push({
      subject,
      credit,
      grade: grade.toUpperCase()
    });
  });

  const students = Array.from(studentMap.values());

  if (students.length === 0) {
    throw new Error('No valid student data found in the CSV file');
  }

  return students;
}

function processExcelData(headers: string[], dataRows: any[][]): StudentData[] {
  if (dataRows.length === 0) {
    throw new Error('Excel file contains no data rows');
  }

  const studentNameCol = findColumn(headers, ['student name', 'name', 'student']);
  const registerNumberCol = findColumn(headers, ['register number', 'registration number', 'reg no', 'register no']);
  const subjectCol = findColumn(headers, ['subject', 'subject name', 'course']);
  const creditCol = findColumn(headers, ['credit', 'credits', 'credit hours']);
  const gradeCol = findColumn(headers, ['grade', 'grades']);

  if (!studentNameCol || !subjectCol || !creditCol || !gradeCol) {
    throw new Error('Excel file must contain columns: Student Name, Subject, Credits, Grade');
  }

  // Group data by student
  const studentMap = new Map<string, StudentData>();

  dataRows.forEach((row, index) => {
    const studentName = row[headers.indexOf(studentNameCol)]?.toString().trim();
    const registerNumber = registerNumberCol ? row[headers.indexOf(registerNumberCol)]?.toString().trim() : '';
    const subject = row[headers.indexOf(subjectCol)]?.toString().trim();
    const creditStr = row[headers.indexOf(creditCol)]?.toString().trim();
    const grade = row[headers.indexOf(gradeCol)]?.toString().trim();

    if (!studentName || !subject || !creditStr || !grade) {
      console.warn(`Skipping row ${index + 2}: missing required data`);
      return;
    }

    const credit = parseFloat(creditStr);
    if (isNaN(credit) || credit <= 0) {
      throw new Error(`Invalid credit value "${creditStr}" in row ${index + 2}`);
    }

    const studentKey = `${studentName}_${registerNumber}`;

    if (!studentMap.has(studentKey)) {
      studentMap.set(studentKey, {
        studentName,
        registerNumber: registerNumber || undefined,
        subjects: []
      });
    }

    const student = studentMap.get(studentKey)!;
    student.subjects.push({
      subject,
      credit,
      grade: grade.toUpperCase()
    });
  });

  const students = Array.from(studentMap.values());

  if (students.length === 0) {
    throw new Error('No valid student data found in the Excel file');
  }

  return students;
}

function findColumn(headers: string[], possibleNames: string[]): string | null {
  for (const name of possibleNames) {
    const found = headers.find(header => header.includes(name));
    if (found) return found;
  }
  return null;
}

export function validateCSVFormat(file: File): Promise<{ valid: boolean; error?: string; preview?: any[] }> {
  return new Promise((resolve) => {
    Papa.parse(file, {
      header: true,
      skipEmptyLines: true,
      transformHeader: (header: string) => header.trim().toLowerCase(),
      preview: 5, // Only parse first 5 rows for validation
      complete: (results) => {
        try {
          if (results.data.length === 0) {
            resolve({ valid: false, error: 'CSV file is empty or contains no valid data' });
            return;
          }

          const firstRow = results.data[0] as any;
          const headers = Object.keys(firstRow);
          
          const studentNameCol = findColumn(headers, ['student name', 'name', 'student']);
          const subjectCol = findColumn(headers, ['subject', 'subject name', 'course']);
          const creditCol = findColumn(headers, ['credit', 'credits', 'credit hours']);
          const gradeCol = findColumn(headers, ['grade', 'grades']);

          if (!studentNameCol || !subjectCol || !creditCol || !gradeCol) {
            resolve({ 
              valid: false, 
              error: 'CSV file must contain columns: Student Name, Subject, Credits, Grade',
              preview: results.data.slice(0, 3)
            });
            return;
          }

          resolve({ 
            valid: true, 
            preview: results.data.slice(0, 5)
          });
        } catch (error) {
          resolve({ 
            valid: false, 
            error: `Failed to validate CSV: ${error instanceof Error ? error.message : 'Unknown error'}` 
          });
        }
      },
      error: (error) => {
        resolve({ valid: false, error: `CSV parsing error: ${error.message}` });
      }
    });
  });
}
